.home-intro-portrait {
  margin: 0;
}

.home-intro-portrait img {
  display: block;
  width: 100%;
  border-radius: 18px;
  box-shadow: var(--shadow);
  object-fit: cover;
  object-position: 50% 15%;
}

.home-intro-grid {
  display: grid;
  grid-template-columns: 1.15fr 1fr;
  gap: 40px;
  align-items: stretch;
  margin: 40px 0;
}

.home-intro-text {
  min-width: 0;
  display: flow-root;
}

.home-intro-text > :first-child {
  margin-top: 0;
}

.home-intro-text > :last-child {
  margin-bottom: 0;
}

.home-intro-portrait {
  min-width: 0;
  display: flex;
  align-items: stretch;
  justify-content: center;
  align-self: stretch;
}

.home-intro-portrait img {
  height: 100%;
  min-height: 0;
  flex: 1 1 auto;
}

@media (max-width: 500px) {
  .home-intro-grid {
    grid-template-columns: 1fr;
    gap: 22px;
    margin: 24px 0;
  }

  .home-intro-portrait img {
    height: auto;
    aspect-ratio: 3 / 2;
    object-position: center;
  }
}

.home-welcome {
  margin: 18px auto 0;
  max-width: 1000px;
  padding: 16px 16px 14px;
  border: 1px solid var(--light-gray);
  border-radius: 18px;
  background: var(--background-alt);
  box-shadow: var(--shadow);
}

.home-welcome-title {
  font-family: var(--heading);
  font-weight: var(--semi-bold);
}

.home-welcome-text {
  margin-top: 8px;
  color: var(--text);
}

.home-welcome-buttons {
  margin-top: 12px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 10px;
}

.home-welcome-buttons .button {
  margin: 0;
  justify-content: center;
}

.home-welcome-small {
  margin-top: 10px;
  color: var(--dark-gray);
  font-size: 0.95rem;
}

.home-welcome-small .button-wrapper {
  display: inline-block;
  vertical-align: middle;
  margin-left: 6px;
}

.home-welcome-small .button {
  margin: 0;
}

@media (max-width: 700px) {
  .home-welcome-buttons {
    grid-template-columns: 1fr;
  }
}
